.post-container {
  display: flex;
  flex-direction: column;
  width: auto;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
}

.post-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.post-section > h1 {
  justify-content: start;
  width: 100%;
  margin-bottom: 10px;
}

.post-section > h4 {
  justify-content: start;
  width: 100%;
  margin-top: 0;
  font-weight: 100;
}

.post-section > img {
  width: 100%;
  margin-top: 10px;
}

.post-section > p {
  margin-top: 30px;
}

.icon-post-section {
  width: 100%;
}

.icon-post-wrapper {
  display: flex;
}

.icon-post {
  margin: 0 10px;
  color: #2ecb89;
}

.icon-post:hover {
  color: #242424;
  cursor: pointer;
}

.other-post-section {
  width: 100%;
}

@media screen and (max-width: 720px) {
  .post-section > h1,
  .other-post-section > h1 {
    font-size: 24px;
  }

  .post-section > h4,
  .post-section > p {
    font-size: 14px;
  }
  .other-post-section > h1 {
    text-align: center;
  }
}
